import React, {useEffect, useState} from 'react';
import { Helmet } from 'react-helmet'
import { Link } from 'react-router-dom';
import ip1 from '../../../assets/images/projects/notes/iP-M1.png'
import ip2 from '../../../assets/images/projects/notes/iP-M2.png'
import ip3 from '../../../assets/images/projects/notes/iP-M3.png'
import ipa1 from '../../../assets/images/projects/notes/iPa-M1.png'
import mb1 from '../../../assets/images/projects/notes/MB-M1.png'
import mb2 from '../../../assets/images/projects/notes/MB-M2.png'
import FramerScroll from '../../../components/project-sections/FramerScroll';
import { motion } from "framer-motion";

function NotesApp() {

    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);

    const [hoveredIndex, setHoveredIndex] = useState(null);

    const handleMouseOver = (index) => {
        setHoveredIndex(index);
    };

    const handleMouseOut = () => {
        setHoveredIndex(null);
    };

    return(
        <div class="animate-in w-[85%] md:w-[700px] mx-auto h-screen dark:bg-black text-L-1 dark:text-primary-d">
            <Helmet>
                <title>Notes</title>
            </Helmet>

            <FramerScroll/>

            {/* <Header/> */}

            <Link to='/projects' className='lg:inline-block hidden'>
                <button className='back-btn flex hover:scale-110 duration-500 items-center justify-center rounded-full top-[120px] -ml-[120px] fixed bg-g-1 text-g-6 dark:text-L-1 dark:bg-gray-100 px-4 py-2'>
                    <ion-icon name="arrow-back" className='flex text-center'/>&nbsp;Back
                </button>
            </Link>

            <aside className='table-of-contents hidden lg:inline-block fixed top-[120px] ml-[730px]'>
                <h4 className='pb-2 font-medium text-L-1 dark:text-secondary-d'>CONTENTS</h4>
                <ul className='flex flex-col gap-3 text-sm'>
                    <li className='tob-li'><a href="#mockups-1">Homescreen</a></li>
                    <li className='tob-li'><a href="#mockups-2">Mobile view</a></li>
                    <li className='tob-li'><a href="#mockups-3">Tablet view</a></li>
                    <li className='tob-li'><a href="#mockups-4">Dashboard view</a></li>
                    <li className='tob-li'><a href="#tags">Tags</a></li>
                </ul>
            </aside>

            <main className="main-default pt-20">
                <p className="animate-in text-L-2 dark:text-secondary-d">2024 · <a class='hover:underline' href='https://github.com/cdemzy/notes-app' target='_blank' rel="noreferrer">Visit Repository</a> </p>
                <h1 className='animate-in text-h1-L dark:text-primary-d font-semibold'>Notes</h1>

                <p className='description text-third-d dark:text-secondary-d pt-4 pb-10'>
                  An open source project that I started on April 2024 to learn the aspects of full-stack development...
                </p>

                <section id='mockups-1' className='flex flex-col items-center justify-center'>
                  <section className='mockup w-full'>
                      <div className='flex items-center justify-center flex-col'>
                          <img className='bg-w-2 dark:bg-g-5 border dark:border-g-1 box-shdw rounded-xl w-full' src={ mb1} alt='Macbook homescreen view' />
                          <p className='mt-2 text-L-2 dark:text-secondary-d text-sm '>Macbook homescreen view.</p>
                      </div>
                  </section>

                <section id='mockups-2' className='mt-5 w-full'>
                    <div className='flex items-center flex-col'>
                        <div className='mobile-views flex justify-start md:justify-center sm:justify-center bg-w-2 dark:bg-g-5 border dark:border-g-1 box-shdw rounded-xl p-5 gap-5 overflow-auto'>
                            <img className='h-[380px] md:h-[510px]' src={ip1} alt='iPhone view of notes' />
                            <img className='h-[380px] md:h-[510px]' src={ip2} alt='iPhone view of notes' />
                            <img className='h-[380px] md:h-[510px]' src={ip3} alt='iPhone view of notes' />
                        </div>
                            <p className='mt-2 text-L-2 dark:text-secondary-d text-sm'>Mobile views.</p>
                    </div>
                </section>


                  <section id='mockups-3' className='mt-5 w-full'>
                      <div className='flex items-center flex-col'>
                          <img className='bg-w-2 dark:bg-g-5 border dark:border-g-1 box-shdw rounded-xl w-full' src={ ipa1 } alt='Macbook dashboard view' />
                          <p className='mt-2 text-L-2 dark:text-secondary-d text-sm'>iPad dashboard view.</p>
                      </div>
                  </section>

                  <section id='mockups-4' className='mt-5 w-full'>
                      <div className='flex items-center flex-col'>
                          <img className='bg-w-2 dark:bg-g-5 border dark:border-g-1 box-shdw rounded-xl w-full ' src={ mb2 } alt='Macbook dashboard view' />
                          <p className='mt-2 text-L-2 dark:text-secondary-d text-sm'>Macbook dashboard view.</p>
                      </div>
                  </section>
                </section>

                <section id='tags' className='mt-10 mb-10'>
                    <p className=''>Tags</p>
                    <ul className="flex flex-wrap gap-2 pt-3 transition-opacity h-full">
                        <li className={`project-tags ${hoveredIndex === null || hoveredIndex === 0 ? 'opacity-100' : 'opacity-50'}`} onMouseOver={() => handleMouseOver(0)} onMouseOut={handleMouseOut}>Full-Stack Development</li>
                        <li className={`project-tags ${hoveredIndex === null || hoveredIndex === 1 ? 'opacity-100' : 'opacity-50'}`} onMouseOver={() => handleMouseOver(1)} onMouseOut={handleMouseOut}>Open Source</li>
                        <li className={`project-tags ${hoveredIndex === null || hoveredIndex === 2 ? 'opacity-100' : 'opacity-50'}`} onMouseOver={() => handleMouseOver(2)} onMouseOut={handleMouseOut}>MERN</li>
                        <li className={`project-tags ${hoveredIndex === null || hoveredIndex === 3 ? 'opacity-100' : 'opacity-50'}`} onMouseOver={() => handleMouseOver(3)} onMouseOut={handleMouseOut}>MongoDB</li>
                        <li className={`project-tags ${hoveredIndex === null || hoveredIndex === 4 ? 'opacity-100' : 'opacity-50'}`} onMouseOver={() => handleMouseOver(4)} onMouseOut={handleMouseOut}>ExpressJS</li>
                        <li className={`project-tags ${hoveredIndex === null || hoveredIndex === 5 ? 'opacity-100' : 'opacity-50'}`} onMouseOver={() => handleMouseOver(5)} onMouseOut={handleMouseOut}>React</li>
                        <li className={`project-tags ${hoveredIndex === null || hoveredIndex === 6 ? 'opacity-100' : 'opacity-50'}`} onMouseOver={() => handleMouseOver(6)} onMouseOut={handleMouseOut}>Node.js</li>
                        <li className={`project-tags ${hoveredIndex === null || hoveredIndex === 7 ? 'opacity-100' : 'opacity-50'}`} onMouseOver={() => handleMouseOver(7)} onMouseOut={handleMouseOut}>Tailwind CSS</li>
                        <li className={`project-tags ${hoveredIndex === null || hoveredIndex === 8 ? 'opacity-100' : 'opacity-50'}`} onMouseOver={() => handleMouseOver(8)} onMouseOut={handleMouseOut}>RESTful APIs</li>
                        <li className={`project-tags ${hoveredIndex === null || hoveredIndex === 9 ? 'opacity-100' : 'opacity-50'}`} onMouseOver={() => handleMouseOver(9)} onMouseOut={handleMouseOut}>Responsive Design</li>
                    </ul>
                </section>

                <motion.div
                whileTap={{ scale: 0.9 }}
                transition={{ type: "spring", stiffness: 400, damping: 10 }}>
                    <Link class='lg:hidden relative bottom-0 hover:border-b border-gray-500 w-max' to='/projects'><ion-icon name="arrow-back-outline"></ion-icon>&nbsp;Back to Projects</Link>
                </motion.div>
            </main>
        </div>
    )
};

export default NotesApp;