import React, { useEffect } from 'react';
import { Helmet } from 'react-helmet';
import ConnectBoxes from '../../components/about-sections/ConnectBoxes';
import Photostack from '../../components/about-sections/Photostack';
import WorkBoxes from '../../components/about-sections/WorkBoxes';
import AboutInfo from '../../components/about-sections/AboutInfo'
import Header from '../../components/navbar/HeaderFixed';

function About() {

    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);
    

    return (
        <div class="w-[100%] md:w-[700px] mx-auto h-[101vh] text-L-1 dark:text-primary-d transition">
            <Helmet>
              <title>About</title>
            </Helmet>

            <Header/>
              
            <div className='circle-pos -z-30 w-[600px] h-[700px] bg-[#FFF]/70 rounded-full fixed top-[300px] blur-[400px] backdrop-blur-[1px]'></div>

            <main class="main-default -z-40">
                <h1 className='animate-in text-h1-L dark:text-primary-d font-semibold'>About</h1>
                <p class="animate-in text-L-2 dark:text-secondary-d" style={{ '--index': 1 }}>Things about me.</p>
                
                
                <section class='photos mt-10 mb-0 sm:mb-20 flex items-center justify-center'>
                    <Photostack/>
                </section>
                
                <section className='about-prgph mt-10 flex flex-col sm:flex-row'>
                    <p class='w-full sm:w-[20%] md:w-[30%] text-L-2 dark:text-secondary-d'>About</p>
                    <AboutInfo/>
                </section>
                
                <section class='connect mt-10 flex flex-col sm:flex-row'>
                    <p class='w-full sm:w-[20%] md:w-[30%] text-L-2 dark:text-secondary-d'>Connect</p>
                    <ConnectBoxes/>
                </section>

                <section class='work mt-10 flex flex-col sm:flex-row'>
                    <p class='w-full sm:w-[20%] md:w-[30%] text-L-2 dark:text-secondary-d'>Work</p>

                    <div class='pt-0 w-full sm:w-[80%] md:w-[70%] text-sm sm:text-base'>

                        <WorkBoxes/>
                        
                    </div>

                </section>
            </main>
        </div>
    )
}

export default About;
