import React, { useEffect} from 'react';
import { Helmet } from 'react-helmet';
import Info from '../../components/home-sections/Info';
import Header from '../../components/navbar/HeaderFixed';

function CharlesDelaCruz() {
  
    useEffect(() => {
      window.scrollTo(0, 0);
    }, []);

    return (
        <div className="relative w-[100%] sm:w-[630px] md:w-[700px] h-[101vh] mx-auto text-L-1 dark:text-primary-d scrollbar-custom dark:dark-scroll-bar">
            <Helmet>
              <title>Charles Dela Cruz</title>
            </Helmet>
            
            <Header/>

            <div className='circle-pos -z-30 w-[700px] h-[900px] bg-[#FFF]/50 rounded-full fixed top-[110px] blur-[900px] backdrop-blur-[200px]'></div>
            
            <main className="z-50 main-default sm:mt-36">
    
              <section className='animate-in mt-5 flex flex-col-reverse sm:flex-row items-center gap-7 text-L-2 dark:text-secondary-d'>
                <Info/>
              </section>
            </main>

        </div>
  )
}

export default CharlesDelaCruz;
