import React, { useState } from 'react';
import { Link, useLocation } from "react-router-dom";
import logo from '../../assets/images/general-logos/logo-light.png';
import logoDark from '../../assets/images/general-logos/logo-dark.png';
import ModeButton from '../settings/ModeButton';
import { motion } from "framer-motion";

const navItems = [
    { to: "/", icon: "home", label: "Home" },
    { to: "/about", icon: "person", label: "About" },
    { to: "/skills", icon: "layers", label: "Skills" },
    { to: "/projects", icon: "folder-open", label: "Projects" },
    { to: "/contact", icon: "call", label: "Contact" },
  ];
  

function Navbar() {

    // Get the current location using useLocation hook
    const location = useLocation();

    const [showMenu, setShowMenu] = useState(false);

    const toggleMenu = () => {
        setShowMenu(!showMenu);
    };

    return (
        <nav class='flex flex-row justify-center items-center w-[100%] py-1 text-L-2 dark:text-neutral-300 mx-auto transition-all duration-500'>

            {/* Mobile */}
            <div className="xs:hidden flex flex-row justify-between items-center gap-2 w-full px-4">
                <div className='flex items-center xs:hidden'>
                    <div className='inline-block dark:hidden'>
                        <Link to="/"><img src={logo} className="max-w-10 max-h-10" alt="Logo"/></Link>
                    </div>
                    <div className='hidden dark:inline-block'>
                        <Link to="/"><img src={logoDark} className="max-w-10 max-h-10" alt="Logo"/></Link>
                    </div>
                </div>
                
                <div className='flex items-center justify-center gap-2'>
                    <button className="menu-button flex justify-center items-center rounded-full text-black dark:text-white active:bg-W-1 active:dark:bg-g-1 w-12" onClick={toggleMenu}><ion-icon name="menu-outline" style={{ fontSize: '30px' }}></ion-icon></button>
                    
                    <ul className={`navbar-list-drop bg-white dark:bg-g-2 z-50 rounded-xl w-[180px] right-[0px] px-2 mt-0 flex flex-col justify-center items-start top-[90px] gap-3  ${showMenu ? 'active border dark:border-g-1' : ''}`}>
                        <li class={`w-full mt-2 py-1 px-3 flex items-start justify-start active:bg-slate-300 hover:bg-slate-100 hover:text-black dark:hover:bg-g-4 rounded-lg dark:hover:text-primary-d ${location.pathname === '/about' ? 'active-link' : ''}`}>
                            <motion.div
                            whileTap={{ scale: 0.9 }}
                            transition={{ type: "spring", stiffness: 400, damping: 10 }}>
                                <Link to="/about" className='flex flex-row gap-2 items-center justify-center'><ion-icon name="person"></ion-icon>About</Link>
                            </motion.div>
                        </li>
                        <li class={`w-full py-1 px-3 flex items-start justify-start active:bg-slate-300 hover:bg-slate-100 hover:text-black dark:hover:bg-g-4 rounded-lg dark:hover:text-primary-d ${location.pathname === '/skills' ? 'active-link' : ''}`}>
                            <motion.div
                            whileTap={{ scale: 0.9 }}
                            transition={{ type: "spring", stiffness: 400, damping: 10 }}>
                                <Link to="/skills" className='flex flex-row gap-2 items-center justify-center'><ion-icon name="layers"></ion-icon>Skills</Link>
                            </motion.div>
                        </li>
                        <li className={`w-full py-1 px-3 flex items-start justify-start active:bg-slate-300 hover:bg-slate-100 hover:text-black dark:hover:bg-g-4 rounded-lg dark:hover:text-primary-d ${location.pathname === '/projects' ? 'active-link' : ''}`}>
                            <motion.div
                            whileTap={{ scale: 0.9 }}
                            transition={{ type: "spring", stiffness: 400, damping: 10 }}>
                                <Link to="/projects" className='flex flex-row gap-2 items-center justify-center'><ion-icon name="folder-open"></ion-icon>Projects</Link>
                            </motion.div>
                        </li>
                        <li className={`w-full mb-2 py-1 px-3 flex items-start justify-start active:bg-slate-300 hover:bg-slate-100 hover:text-black dark:hover:bg-g-4 rounded-lg dark:hover:text-primary-d ${location.pathname === '/contact' ? 'active-link' : ''}`}>
                            <motion.div
                            whileTap={{ scale: 0.9 }}
                            transition={{ type: "spring", stiffness: 400, damping: 10 }}>
                                <Link to="/contact" className='flex flex-row gap-2 items-center justify-center'><ion-icon name="call"></ion-icon>Contact</Link>
                            </motion.div>
                        </li>
                    </ul>
                    <ModeButton/>
                </div>
            </div> 
            
            
            {/* Desktop */}
            <div className=''>
                <ul className='hidden xs:flex flex-row justify-center items-center gap-4'>
                {navItems.map((item) => (
                    <li key={item.to}>
                    <motion.div
                        whileHover={{ scale: 1.1 }}
                        transition={{ type: "spring", stiffness: 400, damping: 10 }}
                    >
                        <Link
                        to={item.to}
                        className={`hover:text-black dark:hover:text-primary-d flex items-center justify-center py-2 px-3 ${location.pathname === item.to ? 'active-link' : ''}`}
                        >
                        <ion-icon name={item.icon} style={{ fontSize: '15pt' }}></ion-icon>

                        </Link>
                        
                    </motion.div>
                    </li>
                ))}
                <li className='font-extrabold select-none hidden xs:inline-block'>—</li>
                <li className='flex items-center justify-center py-2 px-3' style={{ fontSize: '15pt' }}>
                    <motion.div
                    whileHover={{ scale: 1.1 }}
                    whileTap={{ scale: 0.9 }}
                    transition={{ type: "spring", stiffness: 400, damping: 10 }}
                    >
                    <ModeButton />
                    </motion.div>
                </li>
                </ul>
            </div>
        </nav>
    );
}

export default Navbar;
