import React, { useState } from 'react'

function ConnectBoxes(){

    const [hoveredIndex, setHoveredIndex] = useState(null);

    const handleMouseOver = (index) => {
        setHoveredIndex(index);
    };

    const handleMouseOut = () => {
        setHoveredIndex(null);
    };

    // Handle mouse move event to track mouse position
    const handleMouseMove = (e) => {
        const { currentTarget: target } = e;
        const rect = target.getBoundingClientRect();
        const x = e.clientX - rect.left;
        const y = e.clientY - rect.top;

        // Update CSS variables to track mouse position
        target.style.setProperty("--mouse-x", `${x}px`);
        target.style.setProperty("--mouse-y", `${y}px`);
    };


    return(
        <div class='link-boxes w-full sm:w-[80%] md:w-[70%] grid grid-cols-1 xs:grid-cols-2 gap-2 pt-2 sm:pt-0 transition-none text-xs xxs:text-base'>
            
                <a class={`glow-border flex flex-row items-center justify-between border rounded-lg bg-w-2/40 border-gray-300 dark:border-g-1 dark:bg-g-2 px-4 py-4 h-min transition-all duration-300 hover:scale-105 ${hoveredIndex === null || hoveredIndex === 1 ? 'opacity-100' : 'opacity-50'}`} onMouseOver={() => handleMouseOver(1)} onMouseOut={handleMouseOut} onMouseMove={handleMouseMove} href='mailto:cdelacruz@dal.ca' target="_blank" rel="noreferrer" >
                    <div class='flex justify-center items-center'><ion-icon name="mail" class="h-5 w-5"></ion-icon><p class='col-span-4 flex items-center text-sm sm:text-base pl-2 sm:pl-0 ml-2'>Email</p></div>
                    
                    <div class='flex justify-center items-center'><ion-icon name="arrow-forward"></ion-icon></div>
                </a>

                <a class={`glow-border w-full flex flex-row items-center justify-between border rounded-lg bg-w-2/40 border-gray-300 dark:border-g-1 dark:bg-g-2 px-4 py-4 transition-all duration-300 hover:scale-105 ${hoveredIndex === null || hoveredIndex === 2 ? 'opacity-100' : 'opacity-50'}`} onMouseOver={() => handleMouseOver(2)} onMouseOut={handleMouseOut} onMouseMove={handleMouseMove} href='https://www.linkedin.com/company/charles-studios' target="_blank" rel="noreferrer" >
                    <div class='flex justify-center items-center'><ion-icon name="briefcase" class="h-5 w-5"></ion-icon><p class='col-span-4 flex items-center text-sm sm:text-base pl-2 sm:pl-0 ml-2'>Freelance</p></div>
                    
                    <div class='flex justify-center items-center'><ion-icon name="arrow-forward"></ion-icon></div>
                </a>

                <a class={`glow-border w-full flex flex-row items-center justify-between border rounded-lg bg-w-2/40 border-gray-300 dark:border-g-1 dark:bg-g-2 px-4 py-4 transition-all duration-300 hover:scale-105 ${hoveredIndex === null || hoveredIndex === 3 ? 'opacity-100' : 'opacity-50'}`} onMouseOver={() => handleMouseOver(3)} onMouseOut={handleMouseOut} onMouseMove={handleMouseMove} href='https://www.linkedin.com/in/cdemzyy/' target="_blank" rel="noreferrer" >
                    <div class='flex justify-center items-center'><ion-icon name="logo-linkedin" class="h-5 w-5"></ion-icon><p class='col-span-4 flex items-center text-sm sm:text-base pl-2 sm:pl-0 ml-2'>LinkedIn</p></div>
                    
                    <div class='flex justify-center items-center'><ion-icon name="arrow-forward"></ion-icon></div>
                </a>
                <a class={`glow-border flex flex-row items-center justify-between border rounded-lg bg-w-2/40 border-gray-300 dark:border-g-1 dark:bg-g-2 px-4 py-4 w-full transition-all duration-300 hover:scale-105 ${hoveredIndex === null || hoveredIndex === 4 ? 'opacity-100' : 'opacity-50'}`} onMouseOver={() => handleMouseOver(4)} onMouseOut={handleMouseOut} onMouseMove={handleMouseMove} href='https://github.com/cdemzy' target="_blank" rel="noreferrer" >
                    <div class='flex justify-center items-center'><ion-icon name="logo-github" class="h-5 w-5"></ion-icon><p class='col-span-4 flex items-center text-sm sm:text-base pl-2 sm:pl-0 ml-2'>GitHub</p></div>
                    
                    <div class='flex justify-center items-center'><ion-icon name="arrow-forward"></ion-icon></div>
                </a>
        </div>
    )
}

export default ConnectBoxes;