import React, { useEffect } from 'react'
import { Helmet } from 'react-helmet'
import ContactForm from '../../components/contact-sections/ContactForm'
import Header from '../../components/navbar/HeaderFixed'

const Contact = () => {

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div class="ww-[100%] md:w-[700px] mx-auto h-screen text-L-1 dark:text-primary-d transition light-scrollbar pb-[101vh]">
        <Helmet>
          <title>Contact</title>
        </Helmet>

        <Header/>

        <div className='circle-pos -z-30 w-[600px] h-[700px] bg-[#FFF]/70 rounded-full fixed top-[300px] blur-[400px] backdrop-blur-[1px]'></div>

          
        <main class="main-default">
            <h1 className='animate-in text-h1-L dark:text-primary-d font-semibold'>Contact</h1>
            <p class="animate-in text-L-2 dark:text-secondary-d" style={{ '--index': 1 }}>Get in Contact with me!</p>

            <ContactForm/>
        </main>
    </div>
  )
}

export default Contact