import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import reportWebVitals from './reportWebVitals';
import About from './pages/About/About';
import Skills from './pages/Skills/Skills'
import CharlesDelaCruz from './pages/Homescreen/CharlesDelaCruz';
import Contact from './pages/Contact/Contact';
import Projects from './pages/Projects/Projects';
import FlappyBird from './pages/Projects/project-pages/FlappyBird';
import NotesApp from './pages/Projects/project-pages/NotesApp';
import { createBrowserRouter, RouterProvider} from 'react-router-dom';

const router = createBrowserRouter([
  {
    path: "/",
    element: <CharlesDelaCruz/>,
  },
  {
    path: "about",
    element: <About/>,
  },
  {
    path: "skills",
    element: <Skills/>,
  },
  {
    path: "projects",
    element: <Projects/>,
  },
  {
    path: "contact",
    element: <Contact/>,
  },
  {
    path: "projects/flappybird",
    element: <FlappyBird/>,
  },
  {
    path: "projects/notes",
    element: <NotesApp/>,
  },
]);


const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
    <RouterProvider router={router} />
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
