import React from 'react'

function AboutInfo(){
    return(
        <div class='w-full sm:w-[80%] md:w-[70%] flex flex-col gap-2 pt-2 sm:pt-0'>
            <p class='text-sm sm:text-base'>
                Hi, I'm Charles, a Computer Science student at Dalhousie University. 
                I'm currently a CO-OP student that is looking for a career in Tech, primarily in the area of Software Engineering, Front-End and Full-Stack Development, and UI/UX Design.
            </p>

            <p class='text-sm sm:text-base'>
                I have experience on collaborating with a development team and I'm currently seeking internships to further develop my skills and contribute to meaningful projects.
            </p>


        </div>
    )
}

export default AboutInfo;