import React, { useState } from 'react';
import dalLogo from '../../assets/images/general-logos/dal-logo.png';
import charlesStudio from '../../assets/images/general-logos/charlestudio.png';

function WorkBoxes() {
    const [hoveredIndex, setHoveredIndex] = useState(null);

    const handleMouseOver = (index) => {
        setHoveredIndex(index);
    };

    const handleMouseOut = () => {
        setHoveredIndex(null);
    };

    // Handle mouse move event to track mouse position
    const handleMouseMove = (e) => {
        const { currentTarget: target } = e;
        const rect = target.getBoundingClientRect();
        const x = e.clientX - rect.left;
        const y = e.clientY - rect.top;

        // Update CSS variables to track mouse position
        target.style.setProperty("--mouse-x", `${x}px`);
        target.style.setProperty("--mouse-y", `${y}px`);
    };

    return (
        <div className='mt-6 sm:mt-0 grid grid-rows-1 gap-2 transition-opacity'>

            {/* Charles Studio Box */}
            <section className={`glow-border work-box flex flex-row gap-4 bg-w-2/70 dark:border-g-1 dark:bg-g-2 backdrop-blur-[90px] border p-4 rounded-xl transition-all duration-300 hover:scale-105 ${hoveredIndex === null || hoveredIndex === 0 ? 'opacity-100' : 'opacity-50'}`} onMouseOver={() => handleMouseOver(0)} onMouseOut={handleMouseOut} onMouseMove={handleMouseMove}>
                
                <div className='flex items-center w-[30%] xs:w-[10%]'>
                    <a href='https://www.linkedin.com/company/charles-studios' target="_blank" rel="noreferrer">
                        <img src={charlesStudio} alt='dalhousie logo' className='w-full h-full rounded-full transition-transform transform duration-200 hover:scale-110'/>
                    </a>
                </div>
                
                <div className='flex flex-col justify-center w-[100%] xs:w-[60%]'>
                    <div class='flex items-center'>
                        <p class='pr-1 text-sm sm:text-base'>Full Stack Engineer</p>
                        <div class='hover:text-L-2 flex items-center transform duration-200 hover:scale-125'><ion-icon name="information-circle-outline" style={{ fontSize: '12pt'}}></ion-icon></div>
                    </div>
                    
                    <div class='flex items-center'><p className='text-L-2 dark:text-secondary-d text-sm sm:text-base'>Charles Studio ·&nbsp;<em>Freelance</em></p></div>

                    <p className='text-L-2 dark:text-secondary-d text-sm sm:text-base inline-block xs:hidden'>2024 - Present</p>
                </div>

                <div className='xs:flex items-center justify-center w-[30%] hidden'>
                    <p className='text-L-2 dark:text-secondary-d text-sm sm:text-base '>2024 - Present</p>
                </div>

            </section>
            
            {/* Dal Box */}
            <section className={`glow-border work-box flex flex-row gap-4 bg-w-2/70 dark:border-g-1 dark:bg-g-2 backdrop-blur-[90px] border p-4 rounded-xl transition-all duration-300 hover:scale-105 ${hoveredIndex === null || hoveredIndex === 1 ? 'opacity-100' : 'opacity-50'}`} onMouseOver={() => handleMouseOver(1)} onMouseOut={handleMouseOut} onMouseMove={handleMouseMove}>
                
                <div className='flex items-center w-[30%] xs:w-[10%]'>
                    <a href='https://www.dal.ca/' target="_blank" rel="noreferrer"><img src={dalLogo} alt='dalhousie logo' className='w-full h-full rounded-full transition-transform transform duration-200 hover:scale-110'/></a>
                </div>
                
                <div className='flex flex-col justify-center w-[100%] xs:w-[60%]'>
                    <div class='flex items-center'>
                        <p class='pr-1 text-sm sm:text-base'>Junior <span className='hidden xs:inline-block'>Student</span> Developer</p>
                        <div class='hover:text-L-2 flex items-center transform duration-200 hover:scale-125'><ion-icon name="information-circle-outline" style={{ fontSize: '12pt'}}></ion-icon></div>
                    </div>
                    
                    <div class='flex items-center'><p className='text-L-2 dark:text-secondary-d text-sm sm:text-base'>Dalhousie University ·&nbsp;<em>Academic</em></p></div>

                    <p className='text-L-2 dark:text-secondary-d text-sm sm:text-base inline-block xs:hidden'>2024 - 2024</p>
                </div>

                <div className='xs:flex items-center justify-center w-[30%] hidden'>
                    <p className='text-L-2 dark:text-secondary-d text-sm sm:text-base'>2024 - 2024</p>
                </div>

            </section>
        </div>
    );
}

export default WorkBoxes;

