import React from 'react';
import harajuku from '../../assets/images/collage/harajuku.jpg';
import hoover from '../../assets/images/collage/hoover.jpg';
import tree from '../../assets/images/collage/tree.jpg';
import dumbo from '../../assets/images/collage/dumbo.jpg';
import backImageHarajuku from '../../assets/images/collage/back-paper-stock-harajuku.jpg';
import backImageHoover from '../../assets/images/collage/back-paper-hoover.jpg';
import backImageTree from '../../assets/images/collage/back-paper-tree.jpg';
import backImageDumbo from '../../assets/images/collage/back-paper-brooklyn.jpg';
import { motion } from "framer-motion";

function PhotoStack() {

    return (
        <div className='main-one flex flex-row'>
            <motion.div
            whileHover={{ scale: 1.15 }}
            transition={{ type: "spring", stiffness: 150, damping: 10 }}>
            <div class="1st-pic hidden lg:block">
                <div class="group max-h-[320px] max-w-[240px] [perspective:1000px] -ml-[80px] sm:-ml-[25px] sm:-mr-[50px]" style={{ transform: 'rotate(-10deg)' }}>
                    <div class="pic-con relative h-full w-full rounded-2xl shadow-xl transition-all duration-300 [transform-style:perspective-3d] group-hover:[transform:rotateY(180deg)]">
                        <img class="absolute inset-0 h-full w-full rounded-2xl object-cover transform scale-x-[-1] subpixel-antialiased" src={ backImageHarajuku } alt='harajuku back'/>
                        <img class="w-full h-full rounded-2xl [transform-rotateY(180deg)] group-hover:opacity-0 subpixel-antialiased" src={ harajuku } alt='harajuku'/>
                    </div>
                </div>
            </div>
            </motion.div>

            <motion.div
            whileHover={{ scale: 1.15 }}
            transition={{ type: "spring", stiffness: 150, damping: 10 }}>
            <div class="2nd-pic hidden lg:block">
                <div class="group max-h-[280px] max-w-[280px] [perspective:1000px] mt-5 -ml-[40px] -mr-[60px]" style={{ transform: 'rotate(5deg)' }}>
                    <div class="relative h-full w-full rounded-2xl shadow-2xl transition-all duration-300 [transform-style:perspective-3d] md:group-hover:[transform:rotateY(180deg)]">
                        <img class="absolute inset-0 h-full w-full rounded-2xl object-cover transform scale-x-[-1] scale-105 subpixel-antialiased" src={ backImageHoover}  alt='hoover dam back'/>
                        <img class="w-full h-full rounded-2xl shadow-xl [transform-rotateY(180deg)] group-hover:opacity-0 subpixel-antialiased" src={ hoover } alt='hoover dam'/>
                    </div>
                </div>
            </div>
            </motion.div>

            <motion.div
            whileHover={{ scale: 1.15 }}
            transition={{ type: "spring", stiffness: 150, damping: 10 }}>
            <div class="3rd-pic hidden xxs:inline-block">
                <div class="group max-w-[220px] max-h-[220px] xs:max-h-[275px] xs:max-w-[275px] sm:max-h-[270px] sm:max-w-[280px] [perspective:1000px] -ml-[10px] -mr-[70px]" style={{ transform: 'rotate(-5deg)' }}>
                    <div class="relative h-full w-full rounded-2xl shadow-2xl transition-all duration-300 [transform-style:perspective-3d] group-hover:[transform:rotateY(180deg)]">
                        <img class="absolute inset-0 h-full w-full rounded-2xl object-cover transform scale-x-[-1] subpixel-antialiased" src={ backImageTree } alt='joshua tree back'/>
                        <img class="w-full h-full rounded-2xl shadow-xl [transform-rotateY(180deg)] group-hover:opacity-0 subpixel-antialiased" src={ tree } alt='joshua tree'/>
                    </div>
                </div>
            </div>
            </motion.div>
            
            <motion.div
            whileHover={{ scale: 1.15 }}
            transition={{ type: "spring", stiffness: 150, damping: 10 }}>
            <div class="4th-pic">
                <div class="group max-w-[60vw] xxs:max-w-[200px] xxs:max-h-[260px] xs:max-h-[275px] xs:max-w-[220px] sm:max-h-[320px] sm:max-w-[250px] [perspective:1000px] mt-10 lg:mt-0 ml-0 mr-0 xxs:-ml-[50px] xxs:-mr-[10px] lg:-ml-[10px] lg:-mr-[20px]" style={{ transform: 'rotate(10deg)' }}>
                    <div class="relative h-full w-full rounded-2xl shadow-2xl transition-all duration-300 [transform-style:perspective-3d] group-hover:[transform:rotateY(180deg)]">
                        <img class="absolute inset-0 h-full w-full rounded-2xl object-cover transform scale-x-[-1] subpixel-antialiased" src={ backImageDumbo } alt='brooklyn new york back'/>
                        <img class="w-full h-full rounded-2xl shadow-xl  [transform-rotateY(180deg)] group-hover:opacity-0 subpixel-antialiased" src={ dumbo } alt='brooklyn new york'/>
                    </div>
                </div>
            </div>
            </motion.div>
        </div>
    );
}

export default PhotoStack;
