import React from 'react'
import Navbar from './Navbar'

const HeaderFixed = () => {
  return (
    <div className='flex items-center justify-center transition-all duration-1000'>
        <header className='z-50 fixed top-0 p-4 w-[85vw] xs:w-[700px] bg-white/80 backdrop-blur-[0.5rem] dark:bg-g-2/80 flex justify-center items-center border dark:border-g-1 rounded-full shadow-xl mt-2'>
            <Navbar/>
        </header>
    </div>
  )
}

export default HeaderFixed